.more-page-main {
  .collapses-container {
    margin-inline: auto;
    width: 85%;
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 40px;
    margin-bottom: 50px;
    @media screen and (max-width: 375px) {
      gap: 20px;
      width: 100%;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }
}
